import { useEffect } from "react";
import { Helmet } from "react-helmet";

import Header from "./Header";
import Footer from "./Footer";
import ContactBanner from "./ContactBanner";
import CallBtn from "./CallBtn";

import bannerImg from '../assets/img/carousel-img-2.webp'
import galleryImg1 from '../assets/img/bunbo/bunbo-1.webp'
import galleryImg2 from '../assets/img/bunbo/bunbo-2.jpg'
import galleryImg3 from '../assets/img/bunbo/bunbo-3.jpg'
import galleryImg4 from '../assets/img/bunbo/bunbo-4.jpg'
import galleryImg5 from '../assets/img/bunbo/bunbo-5.jpg'
import processImg from '../assets/img/service-process.webp'

function ServiceModel2(){
    useEffect(() => {
                        window.scrollTo(0, 0);
                      }, [])
    return (
        <div className="service-model">
            <Helmet>
                <title>M hnh qun Bn b hu</title>
                <meta name="description" content="Chi tit m hnh qun bn b hu - quy trnh thi cng - chi ph d kin" />
                <meta name="keywords" content="M hnh qun n, m hnh qun bn b hu, qun bn b hu, kinh doanh, qun n, m thc" />
                <meta property="og:title" content="M hnh qun bn b hu" />
                <meta property="og:image" content="thumbnail.jpg" />
                <meta property="og:description" content="Chi tit m hnh qun bn b hu - quy trnh thi cng - chi ph d kin" />
            </Helmet>
            <Header active="4" />
            <div className="service-model-banner mt-114">
                <div className="service-text">
                    <h3 className="">M hnh qun bn b hu</h3>
                </div>
                <img src={bannerImg} alt="banner" />
                {/* <i class="fa-solid fa-arrow-down"></i> */}
            </div>
            <div className="service-model-wrapper app-item" id="service-model-wrapper">
                <h5 className="service-model-subtitle">Gii thiu</h5>
                <p className="service-model-desc">
                Dch v thit k qun cm g ca chng ti lun hng ti s ti gin v gn gi to cm gic thn thin, ti u cng nng v mang li tri nghim thoi mi cho thc khch. Vi i ng thit k sng to, chng ti s gip bn xy dng mt khng gian m cng, hin i, ph hp vi phong cch qun cm g v thng hiu ca bn. Chng ti cam kt mang n gii php thit k hon ho, gip qun ca bn ni bt v thu ht khch hng.
                </p>
                <h5 className="service-model-subtitle">Thng tin tng quan:</h5>
                <ul className="service-model-details">
                <li className="">Din tch mt bng: <b>20m2 - 150m2</b></li>
                    <li className="">S lng bn: <b>5 bn - 30 bn</b></li>
                    <li className="">Quy m: <b>20 - 150 khch</b></li>
                    <li className="">Thi gian thi cng d kin: <b>2 tun - 8 tun</b></li>
                    <li className="">Chi ph d kin: <b> ~ Ch t 90 triu</b></li>
                </ul>
                <div className="service-model-process">
                    <h5 className="service-model-subtitle">Quy trnh t vn - setup:</h5>
                    <div className="service-model-process-main">
                        <img src={processImg} alt="Our service process" />
                    </div>
                </div>
                <div className="service-model-img">
                    <h5 className="service-model-subtitle">Hnh nh mt bng v ni tht qun</h5>
                    <div className="service-model-gallery">
                        <img src={galleryImg1} alt="model 1" loading="lazy"/>
                        <img src={galleryImg2} alt="model 2" loading="lazy"/>
                        <img src={galleryImg3} alt="model 3" loading="lazy"/>
                        <img src={galleryImg4} alt="model 4" loading="lazy"/>
                        <img src={galleryImg5} alt="model 5" loading="lazy"/>
                    </div>
                </div>
            </div>
            <CallBtn />
            <ContactBanner />
            <Footer />
        </div>
    )
}

export default ServiceModel2;