// import { Link } from "react-router";
import { useEffect } from "react";
import { Helmet } from "react-helmet";

import Header from "./Header";
import Footer from "./Footer";
import ContactBanner from "./ContactBanner";

import bannerImg from '../assets/img/kiosk/kiosk-3.webp'
import galleryImg1 from '../assets/img/kiosk/kiosk-1.webp'
import galleryImg2 from '../assets/img/kiosk/kiosk-2.webp'
import galleryImg3 from '../assets/img/kiosk/kiosk-3.webp'
import processImg from '../assets/img/service-process.webp'
import CallBtn from "./CallBtn";

import '../assets/css/service.css'

function ServiceModel3(){
    useEffect(() => {
                        window.scrollTo(0, 0);
                      }, [])
    return (
        <div className="service-model">
            <Helmet>
                <title>M hnh Kiosk</title>
                <meta name="description" content="Chi tit m hnh kiosk - quy trnh thi cng - chi ph d kin" />
                <meta name="keywords" content="Kiosk, m kiosk, kinh doanh nh, m hnh kiosk, take away, kinh doanh t vn" />
                <meta property="og:title" content="M hnh kiosk" />
                <meta property="og:image" content="thumbnail.jpg" />
                <meta property="og:description" content="Chi tit m hnh kiosk - quy trnh thi cng - chi ph d kin" />
            </Helmet>
            <Header active="4" />
            <div className="service-model-banner mt-114">
                <div className="service-text">
                    <h3 className="">M hnh Kiosk </h3>
                </div>
                <img src={bannerImg} alt="banner" />
            </div>
            <div className="service-model-wrapper app-item" id="service-model-wrapper">
                <h5 className="service-model-subtitle">Gii thiu</h5>
                <p className="service-model-desc">
                Kiosk l m hnh kinh doanh tinh gn, hiu qu v c th nhn bn s lng ln m khng b p lc u t vo mt bng, khng ngi ch nh ly li mt bng thu, vi chic kiosk di ng th gi y qu anh ch c th di di bt c u mnh mun min l ni  c nhu cu.
                <p>V tr chin lc cho cc m hnh Kiosk take away: Trng hc, khu trung tm, ta nh vn phng, trm metro </p>
                </p>
                <h5 className="service-model-subtitle">Thng tin tng quan:</h5>
                <ul className="service-model-details">
                    <li className="">Din tch mt bng: <b>10m2 - 15m2</b></li>
                    <li className="">Quy m: <b>take away</b></li>
                    <li className="">Thi gian thi cng d kin: <b>10 ngy</b></li>
                    <li className="">Chi ph d kin: <b> ~ Ch t 50 triu</b></li>
                </ul>
                <div className="service-model-process">
                    <h5 className="service-model-subtitle">Quy trnh thi cng:</h5>
                    <div className="service-model-process-main">
                        <img src={processImg} alt="Our service process" />
                    </div>
                </div>
                <div className="service-model-img">
                    <h5 className="service-model-subtitle">Hnh nh mt bng v ni tht kiosk</h5>
                    <div className="service-model-gallery">
                        <img src={galleryImg1} alt="model 1" loading="lazy"/>
                        <img src={galleryImg2} alt="model 2" loading="lazy"/>
                        <img src={galleryImg3} alt="model 3" loading="lazy"/>                    </div>
                </div>
            </div>
            <CallBtn />
            <ContactBanner />
            <Footer />
        </div>
    )
}

export default ServiceModel3;